.listivo_about_welcome {
	margin-bottom: rem(130);
	
	.container-min {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	&-image, &-text {
		flex: 0 0 47%;
	}

	&-image {
		overflow: hidden;
		border-radius: rem(10);
		max-height: rem(425);
		margin-right: rem(30);
	}

	&-text {
		margin-left: rem(30);
		
		.listivo_title {
			position: relative;
			font-size: rem(36);
			line-height: rem(46);
			font-weight: 700;
			font-family: $font-title;
			margin-bottom: rem(30);

			&::after {
				content: "";
				width: rem(117);
				height: rem(6);
				display: block;
				background: url('data:img/png;base64,') no-repeat;
				background-size: 100%;
				margin-top: rem(3);
			}
		}

		b {
			font-size: rem(18);
			line-height: rem(26);
			font-weight: 500;
			display: block;
			margin-bottom: rem(26);
		}

		p {
			line-height: rem(26);
			color: var(--text-descr);
			margin-bottom: rem(46);
		}
	}
}