.listivo_header_page {
	position: relative;
	height: rem(340);
	overflow: hidden;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin-bottom: rem(120);

	// background: url(../img/header_bg_blue.jpg) no-repeat top center / cover;

	.revision_image {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		// height: 100%;
		z-index: -1;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: bottom;
		}
		
	}

	.container-min {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	.listivo_title {
		font-size: rem(54);
		line-height: rem(76);
		font-weight: 700;
		font-family: 'Comfortaa';
		display: block;
		// margin-bottom: rem(12);
	}


}


.listivo_header_page-col {
	position: relative;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin-bottom: rem(120);
	padding-bottom: rem(32);

	&::after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: rem(12);
		background: url(../img/header_line.png) no-repeat;
		background-size: 100%;
	}

	.revision_block {
		position: relative;
		width: 100%;
		height: rem(240);
		display: flex;
		justify-content: center;
		align-items: center;
		margin-bottom: rem(26);

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(4,24,30,.75);
		}

		.listivo_title {
			position: relative;
			z-index: 1;
			font-size: rem(54);
			line-height: rem(76);
			font-weight: 700;
			font-family: $font-title;
			display: block;
			color: white;
			// margin-bottom: rem(12);
		}
		

		&-image {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			z-index: -1;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				object-position: bottom;
			}
		}

		

	}

	.container-min {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	


}

.listovo_breadcrumb_wrap {
	
	height: rem(100);

	background-color: #edf8fb;

	.container-min {
		height: 100%;
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		justify-content: space-between;
	}
}

.listovo_breadcrumb_border {

	height: rem(100);

	border-bottom: rem(1) solid #e4ebf0;

	.container-min {
		height: 100%;
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		justify-content: space-between;
	}
}

.listovo_breadcrumb {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	a {
		position: relative;
		line-height: rem(46);
		margin-right: rem(38);

		&:hover {
			color: var(--red);
		}

		&::before {
			content: "\f061";
			position: absolute;
			top: rem(2);
			right: rem(-26);
			font-family: 'Font Awesome 5 Pro';
			font-weight: 300;
			color: var(--red);
		}
	}

	span {
		position: relative;
		color: var(--color2_2);

		
	}
}

.listivo_social_group {
	display: flex;
	flex-wrap: wrap;

	a {
		margin-right: rem(5);
		width: rem(44);
		height: rem(44);
		border-radius: 50%;
		background-color: #97cdde;
		display: flex;
		align-items: center;
		justify-content: center;

		&:last-child {
			margin-right: 0;
		}

		&:hover {
			background-color: var(--red);
		}

		i {
			color: white;
		}
	}
}