.listivo_reviews {
	width: 100%;
	margin: 0 auto;
	margin-bottom: rem(115);

	// max-width: 1920px;

	&_wrap {
		position: relative;
		margin: 0 rem(38);
	}

	.title_block {
		margin-bottom: rem(87);
	}

	.swiper-pagination {
		position: relative;
		bottom: auto;
		margin-top: rem(85);

		&-bullet {
			width: rem(15);
			height: rem(3);
			border-radius: 0;
			background-color: rgba(0,0,0,.15);
			opacity: 1;
		}

		&-bullet-active {
			width: rem(25);
			background-color: var(--red);
			margin: 0 rem(10);
		}
	}

	.slider_arrows {
		position: absolute;
		top: 50%;
		margin-top: rem(-64);
		z-index: 1;
		width: 100%;

		&-left {
			position: absolute;
			left: rem(-30);
		}

		&-right {
			position: absolute;
			right: rem(-30);
		}
	}
}

.review_item {
	border-radius: rem(10);
	border: rem(1) solid #e4ebf0;
	background-color: white;
	padding: rem(42) rem(56);

	// max-width: rem(570);

	@include tablet {
		padding: rem(42) rem(38);
	}

	&-top {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;

		&_image {
			width: rem(100);
			height: rem(100);
			border-radius: 50%;
			margin-right: rem(24);
		}

		&_text {
			margin-right: auto;
			display: flex;
			flex-direction: column;
			
			span {
				&:first-of-type {
					font-size: rem(20);
					font-family: 'Comfortaa';
					font-weight: 700;
					margin-bottom: rem(8);
				}

				&:last-of-type {
					font-size: rem(14);
					color: var(--text-descr);
				}
			}
		}

		&-icon {
			i {
				color: #e4f5f9;
				font-size: rem(56);
			}
		}

		.block_stars {
			margin-top: rem(16);
		}
	}

	&-bottom {
		margin-top: rem(32);

		.listivo_text {
			line-height: rem(28);
			color: var(--text-descr);
			margin-bottom: rem(36);
		}

		.listivo_button_lite {
			display: inline-flex;
		}
	}

	
}

.block_stars {
	display: flex;
	flex-wrap: wrap;

	&-item {
		margin-right: rem(3);

		&:last-child {
			margin-right: 0;
		}

		i {
			color: var(--color3);
		}
	}

	.gray {
		i {
			color: var(--text-descr) !important;
		}
	}
}