.listivo_seaching {
	position: relative;
	margin-bottom: rem(120);

	.title_block {
		position: relative;
		margin-bottom: rem(85);

		&::after {
			content: "";
			position: absolute;
			top: rem(36);
			right: rem(256);
			width: rem(29);
			height: rem(29);
			background: url(../img/triangle.png) no-repeat;
			background-size: 100%;
		}
	}

	&_items {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	&_item {
		width: rem(360);
		height: rem(562);
		margin-right: rem(30);
		border-radius: rem(10);
		border: rem(1) solid var(--color2_3);
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: rem(62) rem(36);

		@include tablet {
			width: rem(400);
		}

		&:first-child {
			position: relative;

			&::after {
				content: "";
				position: absolute;
				top: 50%;
				margin-top: rem(-30);
				right: rem(-65);
				width: rem(132);
				height: rem(44);
				background: url(../img/arrow_red2.png) no-repeat;
				background-size: 100%;
				z-index: 1;

				@include tablet {
					right: rem(-100);
				}
			}
		}

		&:last-child {
			margin-right: 0;
		}

		&:nth-child(2) {
			background-color: var(--color2_3);
		}

		img {
			margin-bottom: rem(56);
		}

		&-text {
			display: flex;
			flex-direction: column;
			align-items: center;

			.listivo_title {
				font-size: rem(24);
				line-height: rem(32);
				font-family: 'Comfortaa';
				font-weight: 700;
				margin-bottom: rem(26);
				display: block;
				text-align: center;
			}

			.listivi_text {
				text-align: center;
				line-height: rem(28);
			}
		}
	}
}

.listivo_seaching-second {

	background-color: var(--color2_3);
	padding: rem(114) 0;

	&::before {
		content: "";
		position: absolute;
		top: 0;
		right: 0;
		width: 50%;
		height: 100%;
		background-color: white;
		clip-path: polygon(11% 0, 100% 0, 100% 100%, 70% 100%);
		z-index: 0;
	}

	.container-min {
		position: relative;
		z-index: 1;
	}

	.title_block {

		&::after {
			content: "";
			position: absolute;
			top: rem(36);
			left: rem(175);
			width: rem(29);
			height: rem(29);
			background: url(../img/triangle4.png) no-repeat;
			background-size: 100%;
		}
	}

	.listivo_seaching_item {
		border: rem(1) solid #e4ebf0;
		border-radius: rem(10);
		background-color: white;

		&:hover {
			box-shadow: $item_shadow;
		}
	}
}