.listivo_list_page {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;

	margin-top: rem(92);
	margin-bottom: rem(120);

	&-prev,
	&-next {
		width: rem(32);
		height: rem(32);
		display: flex;
		align-items: center;
		justify-content: center;

		i {
			font-size: rem(14);
			color: var(--text-color);
		}
	}

	&-prev {
		margin-right: rem(20);
	}
	&-next {
		margin-left: rem(20);
	}

	&-nav {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		li {
			margin-right: rem(10);

			&:last-child {
				margin-right: 0;
			}

			> span, > a {
				width: rem(50);
				height: rem(50);
				display: flex;
				align-items: center;
				justify-content: center;
				border-radius: 50%;
				border: rem(1) solid transparent;
			}

			>span:not(.ellipsis) {
				background-color: var(--red);
				color: white;
				border-color: var(--red);
			}

			.ellipsis {
				font-size: rem(18);
				width: rem(50);
			}

			> a {
				border-color: #e4ebf0;
				color: var(--text-color);
				transition: all 300ms ease;

				&:hover {
					border-color: var(--red);
					background-color: var(--red);
					color: white;
				}
			}
		}
	}
}