.listivi_page_404 {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding-top: rem(160);

	&-image {
		position: relative;
		pointer-events: none;

		&::before {
			content: "";
			position: absolute;
			top: rem(120);
			right: rem(100);
			width: rem(29);
			height: rem(29);
			background: url(../img/triangle.png) no-repeat;
			background-size: 100%;
		}
	}

	&-content {
		position: relative;
		top: rem(-120);
	}


	.listivo_title {
		position: relative;
		font-size: rem(178);
		line-height: rem(172);
		font-weight: 800;
		color: var(--color2);
		// margin-bottom: rem(16);

		display: flex;
		flex-direction: column;
		align-items: center;

		&::after {
			content: "";
			width: rem(217);
			height: rem(10);
			display: block;
			background: url('data:img/png;base64,') no-repeat;
			background-size: 100%;
			margin-bottom: rem(24);
		}

		&::before {
			content: "";
			position: absolute;
			top: rem(-100);
			left: 50%;
			margin-left: rem(-16);
			width: rem(83);
			height: rem(62);
			background: url(../img/arrow_red.png) no-repeat;
			background-size: 100%;

			transform: rotate(180deg);
		}
	}

	.listivo_text {
		font-size: rem(36);
		font-family: $font-title;
		font-weight: 400;

		margin-bottom: rem(60);
	}
}