.listivo_contact_page {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin-bottom: rem(120);

	.w-5\/12 {
		padding-right: rem(100);
	}

	h1 {
		position: relative;
		font-size: rem(34);
		font-weight: 700;
		font-family: $font-title;
		margin-bottom: rem(40);

		&::before {
			content: "";
			position: absolute;
			top: rem(-15);
			left: rem(-78);
			width: rem(29);
			height: rem(29);
			background: url(../img/triangle.png) no-repeat;
			background-size: 100%;
		}

		&::after {
			content: "";
			width: rem(117);
			height: rem(6);
			display: block;
			background: url('data:img/png;base64,') no-repeat;
			background-size: 100%;
			margin-top: rem(3);
		}
	}

	b {
		font-size: rem(18);
		font-weight: 600;
		margin-bottom: rem(32);
		display: block;
		max-width: rem(320);
	}

	p {
		line-height: rem(28);
		color: var(--text-descr);
		margin-bottom: rem(40);
	}

	&_control {
		display: flex;
		flex-wrap: wrap;
		flex-direction: column;

		&-top {
			display: flex;
			flex-wrap: wrap;

			.listivo_image {
				width: rem(26);
				margin-right: rem(16);
			}

			.listivo_text {
				max-width: rem(320);
				b,span {
					display: block;
				}

				b {
					font-weight: 800;
					color: var(--red);
					font-size: rem(30);
					line-height: rem(28);
					margin-bottom: rem(14);
				}

				span {
					font-size: rem(18);
				}
			}
		}

		.block_social_group {
			margin-left: rem(45);
		}
	}
}

.listivo_map {
	position: relative;
	height: rem(435);
	overflow: hidden;
	border-radius: rem(10);

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	&-mark {
		position: absolute;
		z-index: 10;
		top: rem(200);
		right: rem(185);
		width: rem(45);
		height: rem(45);
		border-radius: 50%;
		background-color: rgba(218,28,46,.3);

		display: flex;
		align-items: center;
		justify-content: center;

		&::before {
			content: "";
			width: rem(25);
			height: rem(25);
			border-radius: 50%;
			background-color: var(--red);
		}
	}
}