.listivo_featured {
	// display: grid;
	// grid-template-columns: calc((100% - 1140px)/2) 1fr;
	background-color: var(--color2_3);
	padding: rem(114) 0;
	position: relative;
	overflow: hidden;
	margin-bottom: rem(120);

	&::before {
		content: "";
		position: absolute;
		top: 0;
		right: 0;
		width: 50%;
		height: 100%;
		background-color: white;
		clip-path: polygon(11% 0, 100% 0, 100% 100%, 70% 100%);
		z-index: 0;
	}

	&_header {
		position: relative;
		z-index: 1;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-bottom: rem(60);

		.title_block {
			align-items: flex-start;
		}

		.slider_arrows {
			margin-top: rem(40);
		}

		&-right {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			.slider_arrows {
				display: none;
			}
			.slider_arrows.active {
				display: flex;
			}

			> a {
				height: rem(44);
				display: flex;
				align-items: center;
				padding: 0 rem(30);
				color: white;
				font-size: rem(14);
				font-weight: 500;
				background-color: var(--red);
				border-radius: rem(22);
				margin-right: rem(22);
			}
		}
	}

	&_box {
		position: relative;
		z-index: 1;
		width: 1140px;
		margin: 0 auto;

		&-content {
			width: 100vw;

			.swiper-container {
				// position: relative;
				display: none;
				padding: rem(20);
				margin: rem(-20);
				width: 100%;
				height: 100%;

				.swiper-wrapper {
					// padding: rem(20);
					// margin: rem(-20);
					display: flex;
					// align-items: baseline;
				}

				.slider_arrows {
					position: absolute;
					top: 0;
					right: 0;
				}
			}

			.swiper-container.active {
				display: block;
			}

			.swiper-slide {
				max-width: rem(332) !important;
				height: auto;
				display: flex;
				

				@include tablet {
					// max-width: rem(332) !important;
				}
			}
		}
	}
}

.slider_arrows {
	display: flex;
	flex-wrap: wrap;

	&-right {
		i {
			margin-left: rem(4);
		}
	}

	&-left {
		margin-right: rem(10);
	}

	&-left,
	&-right,
	.slider_arrow {
		border-radius: 50%;
		width: rem(60);
		height: rem(60);
		background-color: var(--color2);
		border: rem(1) solid var(--color2);
		display: flex;
		align-items: center;
		justify-content: center;

		box-shadow: 0 0 rem(27) rem(2.4) rgba(0, 0, 0, .08);

		i {
			color: white;
		}

		// &:hover {
		// 	background-color: var(--color2);
		// 	border-color: var(--color2);
		// 	i {
		// 		color: white;
		// 	}
		// }

		// i {
		// 	color: var(--text-color);
		// 	// font-size: rem(18);
		// }
	}

	.swiper-button-disabled {
		background-color: white;
		border: rem(1) solid rgb(228, 235, 240);
		cursor: default;

		i {
			color: var(--text-color);
			// font-size: rem(18);
		}
	}
}

.card_item {
	// display: flex;
	// flex-direction: column;
	// align-items: ;
	// width: rem(360);
	// height: 100%;
	display: flex;
	flex-direction: column;
	max-width: rem(332) !important;
	width: 100%;
	background-color: white;
	box-shadow: 0 rem(7) rem(20) rem(2) rgba(7, 48, 61, 0.07);

	&-top {
		position: relative;
		// margin-bottom: rem(32);
		overflow: hidden;
		border-radius: rem(10);
		height: rem(270);
		background-color: #cccccc;
		padding: rem(32);

		&::before {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgb(0, 0, 0) 100%);
			height: 50%;
			z-index: 1;
		}

		&_preview {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
			z-index: 0;
		}

		&_cat {
			position: absolute;
			top: rem(32);
			left: rem(32);
			background-color: var(--color3_1);
			font-size: rem(11);
			font-weight: 500;
			color: white;
			border-radius: rem(5);
			padding: rem(7) rem(11);
			z-index: 1;
		}

		.listivo_button_r-icon {
			position: absolute;
			top: rem(32);
			right: rem(32);
			z-index: 1;
		}

		&_author {
			position: absolute;
			z-index: 2;
			bottom: rem(32);
			left: rem(32);

			display: flex;
			flex-wrap: wrap;
			align-items: center;

			.listivo_image {
				width: rem(50);
				height: rem(50);
				border-radius: rem(50);
				overflow: hidden;
				margin-right: rem(8);
			}

			.listivo_text {
				color: white;
				font-size: rem(14);
			}
		}
	}

	&-bottom {
		position: relative;
		// bottom: rem(7);
		background-color: white;
		
		&::after {
			content: "";
			// position: absolute;
			// bottom: 0;
			// left: rem(-32);
			// padding-right: rem(64);
			// margin-right: rem(-64);
			width: 100%;
			height: rem(1);
			display: block;
			background-color: #e4ebf0;
		}

		&_head {
			padding: rem(42) rem(32) rem(20) rem(32);
		}

		

		.listivo_title {
			font-size: rem(24);
			line-height: rem(30);
			font-family: 'Comfortaa';
			font-weight: 700;
			margin-bottom: rem(16);
			// letter-spacing: ;
			display: block;

			@include tablet {
				font-size: rem(22);
			}

			&:hover {
				color: var(--red);
			}
		}

		ul {
			// position: relative;
			display: flex;
			flex-wrap: wrap;
			// margin-bottom: rem(20);

			

			li {
				margin-right: rem(8);
				margin-bottom: rem(5);

				&:last-child {
					margin-right: 0;
				}

				a {
					font-size: rem(14);
					font-weight: 500;
					background-color: #fff3f4;
					border-radius: rem(5);
					padding: rem(7) rem(11);
					display: block;
				}
			}
		}
	}

	&-foot {
		position: relative;
		// margin-top: auto;
		// bottom: rem(7);
		background-color: white;
		padding: rem(20) rem(32) rem(32) rem(32);

		&_price {
			font-size: rem(30);
			font-family: 'Comfortaa';
			font-weight: 700;
			color: var(--color2);
			display: block;
			margin-bottom: rem(24);
		}
		&_text {
			font-size: rem(14);
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			i {
				font-size: rem(18);
				color: var(--red);
				margin-right: rem(8);
			}
		}
		&_gray {
			margin-top: rem(7);
			font-size: rem(12);
			color: #878787;
			display: block;
		}
	}
}

.list_button {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: rem(40);

	.listivo_button_white {
		margin-right: rem(14);

		&:last-child {
			margin-right: 0;
		}
	}
}

