.listivo_discover {
	.title_block {
		margin-bottom: rem(80);

		&-bottom {

			&::before {
				content: "";
				position: absolute;
				top: 0;
				left: rem(-110);
				width: rem(83);
				height: rem(62);
				transform: rotate(-180deg);
				background: url(../img/arrow_red.png) no-repeat;
				background-size: 100%;
			}
		}
	}

	&_items {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		
		> svg {
			position: absolute;
			right: 100vw;
			pointer-events: none;
			z-index: -1;
		}
	}

	&_item {
		position: relative;
		// width: 100%;
		width: rem(360);
		// height: 100%;
		height: rem(350);
		display: flex;
		align-items: center;
		justify-content: center;
		overflow: hidden;
		margin-bottom: rem(60);
		clip-path: url(#discover_item);

		@include tablet {
			width: rem(380);
			height: rem(370);
		}

		&:hover {
			.listivo_discover_item-info {
				transform: translateY(0%);
			}
		}

		&-text {
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			font-family: 'Comfortaa';
			font-weight: 700;
			font-size: rem(30);
			color: white;
			position: absolute;
			left: 0;
			height: 100%;
			background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, .7) 50%, rgba(0, 0, 0, 0) 100%);
		}

		&-info {
			position: absolute;
			left: 0;
			bottom: 0;
			width: 100%;
			height: rem(90);
			background-color: var(--red);
			display: flex;
			padding-top: rem(14);
			// align-items: center;
			justify-content: center;

			transition: transform 300ms ease;
			transform: translateY(100%);

			span {
				color: white;
				font-weight: 500;
			}
		}
	}
}