.listivo_services_items {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin-bottom: rem(74);

	&::before {
		content: "";
		position: absolute;
		bottom: rem(-62);
		left: rem(-82);
		width: rem(29);
		height: rem(29);
		background: url(../img/triangle.png) no-repeat;
		background-size: 100%;
	}

	.listivo_services_item {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: center;

		width: rem(205);
		height: rem(215);
		border-radius: rem(10);
		padding: rem(28);
		
		box-shadow: 0 rem(7) rem(20) rem(2) rgba(7, 48, 61, 0.07);


		&:hover {
			background-color: var(--color2);

			.listivo_services_item-bottom {
				span {
					color: white;
				}
			}

			.listivo_services_item-overlay,
			.listivo_services_item-icon-m {
				fill: var(--color2_1) !important;
			}

			.listivo_services_item-icon {
				fill: white;
			}
		}

		&-top {
			position: relative;
			width: rem(106);
			height: rem(103);
			margin-bottom: rem(22);
		}

		&-bottom {
			span {
				font-size: rem(24);
				font-family: 'Comfortaa';
				font-weight: 700;
			}
		}

		&-overlay {
			position: absolute;
			top: 0;
			left: 0;
			width: rem(106);
			height: rem(103);
			z-index: 0;
		}

		&-icon {
			position: relative;
			z-index: 1;
			width: rem(70);
			margin-top: rem(18);
			margin-left: rem(20);

			fill: var(--text-color);
		}

		&-icon-m {
			position: absolute;
			// top: 0;
			// left: 0;
		}
	}
	.listivo_services_item.item_1 {
		.listivo_services_item-icon-m {
			position: absolute;
			top: rem(-8);
			left: rem(-42);
		}

		.listivo_services_item-overlay,
		.listivo_services_item-icon-m {
			fill: var(--color4);
		}
	}
	.listivo_services_item.item_2 {
		.listivo_services_item-icon-m {
			position: absolute;
			bottom: rem(-20);
			left: rem(-32);
		}

		// .listivo_services_item-overlay,
		// .listivo_services_item-icon-m {
		// 	fill: var(--color2_1);
		// }

		// .listivo_services_item-icon {
		// 	fill: white;
		// }

		// background-color: var(--color2_2);

		// .listivo_services_item-bottom {
		// 	span {
		// 		color: white;
		// 	}
		// }

		.listivo_services_item-overlay,
		.listivo_services_item-icon-m {
			fill: var(--color5_2);
		}
	}

	.listivo_services_item.item_3 {
		.listivo_services_item-icon-m {
			position: absolute;
			bottom: rem(-32);
			right: rem(-32);
		}

		.listivo_services_item-overlay,
		.listivo_services_item-icon-m {
			fill: var(--color5);
		}
	}

	.listivo_services_item.item_4 {
		.listivo_services_item-icon-m {
			position: absolute;
			top: rem(-8);
			left: rem(-42);
		}

		.listivo_services_item-overlay,
		.listivo_services_item-icon-m {
			fill: var(--color5_1);
		}
	}
	.listivo_services_item.item_5 {
		.listivo_services_item-icon-m {
			position: absolute;
			bottom: rem(-42);
			left: rem(-32);
		}

		.listivo_services_item-overlay,
		.listivo_services_item-icon-m {
			fill: var(--color5_2);
		}
	}
}

.listivo_services_text {
	position: relative;
	text-align: center;
	margin-bottom: rem(240);

	&::after {
		content: "";
		position: absolute;
		top: rem(-30);
		right: rem(80);
		width: rem(83);
		height: rem(62);
		background: url(../img/arrow_red.png) no-repeat;
		background-size: 100%;
	}

	.listivo_text {
		font-size: rem(30);
		font-weight: 300;
	}
}