.card_counts {
	position: relative;
	margin-bottom: rem(240);

	padding-top: rem(165);

	

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 50%;
		margin-left: rem(-40);
		width: rem(83);
		height: rem(62);
		background: url(../img/arrow_red.png) no-repeat;
		background-size: 100%;

		transform: rotate(180deg);
	}

	.container-min {
		position: relative;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		&::after {
			content: "";
			position: absolute;
			top: rem(-165);
			left: 0;
			width: 100%;
			height: rem(412);
			background: url(../img/about_map.png) no-repeat;
			background-size: 100%;
			z-index: -1;
		}
	}

	&_item {
		width: rem(260);
		height: rem(340);
		margin-right: rem(30);
		display: flex;
		flex-wrap: wrap;
		flex-direction: column;
		justify-content: center;
		align-items: center;

		@include tablet {
			width: rem(300);
		}

		&:first-child {
			background-color: var(--color2_4);
			border-top-left-radius: rem(20);
		}

		&:nth-child(2) {
			background-color: var(--red);
			border-top-right-radius: rem(20);
		}

		&:nth-child(3) {
			background-color: var(--color6);
			border-bottom-left-radius: rem(20);
		}
		

		&-image {
			display: flex;
			justify-content: center;
			width: rem(100);
			height: rem(120);
			margin-bottom: rem(24);
			svg {
				max-width: rem(80);
				fill: white;
			}
		}

		&-text {
			display: flex;
			flex-direction: column;
			align-items: center;

			b {
				color: white;
				font-size: rem(54);
				line-height: rem(46);
				font-weight: 700;
				font-family: $font-title;
				margin-bottom: rem(20);
			}

			span {
				font-size: rem(24);
				line-height: rem(32);
				color: white;
			}
		}

		&:last-child {
			background-color: var(--color3_1);
			border-bottom-right-radius: rem(20);
			margin-right: 0;
		}
	}
}