.listivo_subscribed {
	// background-color: #093b4b;
	height: rem(270);
	width: 100%;

	background: #093b4b url(../img/subscribe_bg.png) repeat-x left top;

	.container-min {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		height: 100%;
	}

	&_left {
		width: 45%;

		.listivo_title {
			position: relative;
			font-size: rem(42);
			line-height: rem(52);
			font-size: 700;
			font-family: 'Comfortaa';
			color: white;

			&::before {
				content: "";
				position: absolute;
				bottom: 0;
				left: 0;
				width: rem(293);
				height: rem(8);
				background: url(../img/line3.png) no-repeat;
				background-size: 100%;
			}
		}
	}

	&_right {
		width: 55%;
	}

	&_field {
		position: relative;

		input {
			width: 100%;
			height: rem(85);
			background-color: white;
			color: var(--text-color);
			border-radius: rem(15);
			font-size: rem(18);
			padding: 0 rem(250) 0 rem(28);

			@include placeholder {
				color: var(--text-color);
			}
		}
		.listivo_button {
			position: absolute;
			top: rem(10);
			right: rem(10);
			height: rem(65);
		}
	}
}