.listivo_header_bottom {
	display: grid;
	grid-template-columns: calc((100% - 1650px)/2) 4fr 6fr calc((100% - 1650px)/2);
	margin-bottom: rem(130);

	@include tablet {
		grid-template-columns: calc((100% - 1320px)/2) 4fr 6fr calc((100% - 1320px)/2);
	}

	&-info {
		position: relative;
		grid-column: 2 / span 1;

		display: flex;
		flex-direction: column;
		justify-content: center;

		.listivo_arrow {
			position: absolute;
			right: rem(50);
			bottom: rem(320);
			transform: rotate(-30deg);

			@include tablet {
				right: rem(20);
				bottom: rem(290);
			}
		}

		&_list {
			position: relative;
			margin-top: rem(115);

			b {
				font-weight: 600;
				margin-right: rem(12);
			}

			a {
				color: var(--text-descr);
				margin-right: rem(8);

				&:hover {
					color: var(--red);
					text-decoration: underline;
				}

				&:last-child {
					margin-right: 0;
					&::after {
						display: none;
					}
				}

				&::after {
					content: ",";
					color: var(--text-descr) !important;
				}
			}

			&::before {
				content: "";
				position: absolute;
				bottom: rem(-60);
				left: rem(53);
				width: rem(55);
				height: rem(41);
				background: url(../img/arrow_blue.png) no-repeat;
				background-size: 100%;
			}

			&::after {
				content: "";
				position: absolute;
				bottom: rem(-82);
				right: rem(24);
				width: rem(65);
				height: rem(35);
				background: url(../img/wavy.png) no-repeat;
				background-size: 100%;

			}
		}

		&_subtitle {
			position: absolute;
			top: 14%;
			left: rem(-68);
			// margin-bottom: rem(25);
			// font-size: rem(38);
			// font-family: var(--font-brittany);
			// color: var(--red);
		}

		.listivo_title {
			position: relative;
			font-size: rem(70);
			line-height: rem(80);
			font-weight: 800;
			letter-spacing: rem(1.25);
			margin-bottom: rem(58);

			> span {
				color: var(--color2);
			}

			@include tablet {
				font-size: rem(68);
			}

			&::before {
				content: "";
				position: absolute;
				top: rem(-60);
				left: rem(-52);
				width: rem(431);
				height: rem(50);
				
				background: url(../img/looking_for_something.png) no-repeat;
				background-size: 100%;
				z-index: 1;
			}

			&::after {
				content: "";
				position: absolute;
				top: rem(70);
				right: rem(204);
				width: rem(284);
				height: rem(13);
				background: url(../img/line.png) no-repeat;
				z-index: -1;

				@include tablet {
					width: rem(290);
					right: rem(168);
				}
			}
		}
	}

	&-picture {
		
		grid-column-end: 2 span;

		img {
			width: 100%;
			background-size: cover;
		}
	}
}

.listivo_search_form {
	position: absolute;
	top: 0;
	left: 0;
	width: rem(845);
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
	padding: rem(10);
	box-shadow: 0 rem(5) rem(38) rem(7) rgba(0, 0, 0, 0.09);
	background-color: white;
	border-radius: rem(15);

	margin-bottom: rem(38);

	@include tablet {
		width: rem(800);
	}

	&-field {
		margin: 0 rem(16);
		display: flex;
		align-items: center;

		input {
			font-size: rem(18);
			
			&:last-child {
				max-width: rem(210);
			}

			@include tablet {
				font-size: rem(16);
			}

			@include placeholder {
				color: var(--text-color);
			}
		}

		i {
			margin-right: rem(12);
			color: var(--text-color);
		}
	}

	.listivo_button {
		padding: 0 rem(96) 0 rem(25);
	}
}

#home_2 {
	position: relative;
	.listivo_search_form {
		width: rem(880);

		@include tablet {
			width: rem(830);
		}
	}

	.header_bottom-picture {
		// position: relative;

		&::before {
			content: "";
			position: absolute;
			// z-index: 1;
			top: 0;
			right: rem(400);
			width: rem(29);
			height: rem(29);
			background: url(../img/triangle2.png) no-repeat;
			background-size: 100%;

			@include tablet {
				top: rem(-20);
				right: rem(370);
			}
		}
	}
}

#home3 {
	
	grid-template-columns: calc((100% - 1650px)/2) 5fr 5fr calc((100% - 1650px)/2);
	margin-bottom: rem(264);

	@include tablet {
		grid-template-columns: calc((100% - 1320px)/2) 5fr 5fr calc((100% - 1320px)/2);
	}

	&::after {
		content: "";
		position: absolute;
		// z-index: 1;
		top: rem(185);
		left: rem(185);
		width: rem(29);
		height: rem(29);
		background: url(../img/triangle2.png) no-repeat;
		background-size: 100%;

		@include tablet {
			top: rem(185);
			left: rem(185);
		}
	}

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		width: 100%;
		height: rem(874);
		background: url(../img/header_bg_full.png) no-repeat top center;
		background-size: cover;

		@include tablet {
			height: rem(760);
		}
	}

	.header_bottom-info {
		padding-top: rem(165);
	}

	.header_bottom-info_list::after {
		right: rem(156);
		bottom: rem(-152);

		@include tablet {
			bottom: rem(-126);
		}
	}

	.header_bottom-info_list::before {
		display: none;
	}

	.listivo_title {
		&::after {
			right: rem(190);

			@include tablet {
				right: rem(150);
			}
		}
	}

	.listivo_arrow {
		bottom: rem(265);

		@include tablet {
			bottom: rem(245);
		}
	}

	.listivo_search_form {
		width: rem(750);

		&-field {
			input {
				&:last-child {
					max-width: rem(180);
				}
			}
		}
	}

	.header_bottom-picture {
		text-align: right;

		img {
			margin-top: rem(30);
			width: 85%;
		}
	}


}