.listivo_user_page {

	background-color: #f2fafc;
	padding: rem(100) 0;

	.container-min {
		display: flex;
		flex-wrap: wrap;
	}

	p.listivo_text {
		line-height: rem(28);
		color: var(--text-descr);
		margin-bottom: rem(28);
	}

	.listivo_block_border:not(.p-46) {
		display: inline-flex;
		background-color: transparent;
	}
	.listivo_block_border.p-46 {
		background-color: transparent;
		border: 0;
		padding: 0 0 0 rem(75);

		.listivo_button {
			width: auto;
		}

		.listivo_checked {
			margin-top: rem(20);
			margin-bottom: rem(45);
		}
	}

	&-info {
		padding-bottom: rem(28);
		border-bottom: rem(1) solid #cee1e6;
		margin-bottom: rem(42);

		display: flex;
		flex-wrap: wrap;

		.listivo_image {
			width: rem(150);
			height: rem(150);
			border-radius: 50%;
			overflow: hidden;
			margin-right: rem(40);
		}

		.listivo_info {

			.listivo_title {
				font-size: rem(36);
				font-weight: 700;
				font-family: $font-title;
				margin-bottom: rem(8);
			}

			> span {
				font-size: rem(14);
				color: var(--text-descr);
				margin-bottom: rem(18);
				display: block;
			}

			.social_item {
				background-color: white;

				i {
					color: rgba(11,67,85,.6);
				}

				&:hover {

					i {
						color: white;
					}
					
				}
			}
		}
	}
}

.listivo_listing {
	margin: rem(100) 0;
}