.listivo_field_button {
	width: 100%;
	position: relative;
	border-radius: rem(10);
	overflow: hidden;
	height: rem(64);

	input {
		border: rem(1) solid #e4ebf0;
		height: 100%;
		width: 100%;
		border-radius: rem(10);
		padding: rem(20) rem(80) rem(20) rem(26);

		@include placeholder {
			color: var(--text-color);
		}
	}

	button {
		position: absolute;
		top: rem(-1);
		right: rem(-1);
		height: 100%;
		padding-left: rem(5);
		display: flex;
		align-items: center;
		justify-content: center;
		color: var(--text-color);
		background-color: var(--red);
		width: rem(65);
		clip-path: polygon(30% 0, 100% 0, 100% 100%, 0% 100%);
	}
}

.listivo_block_border {
	border-radius: rem(10);
	padding: rem(20) rem(46);
	background-color: white;
	border: rem(1) solid #e4ebf0;

	>.listivo_title {
		font-size: rem(20);
		font-weight: 700;
		font-family: $font-title;
		display: inline-block;
		margin-bottom: rem(20);

		@include tablet {
			font-size: rem(24);
		}

		&::after {
			content: "";
			width: 100%;
			height: rem(9);
			display: block;
			background: url('data:img/png;base64,') no-repeat;
			background-size: 100%;
			margin-top: rem(3);
		}
	}

	>.listivo_text {
		line-height: rem(28);
		color: var(--text-descr);
	}

	.listivo_field {
		margin-bottom: rem(14);

		&:first-child {
			margin-top: rem(36);
		}
	}

	.listivo_field_control {
		margin-top: rem(16);

		.listivo_button {
			width: 100%;
		}
	}
}

.listivo_block_blue {
	border-radius: rem(10);
	padding: rem(46);
	background-color: var(--color2_3);

	> .listivo_title {
		font-size: rem(20);
		font-weight: 700;
		font-family: $font-title;
		display: inline-block;
		margin-bottom: rem(42);

		@include tablet {
			font-size: rem(24);
		}

		&::after {
			content: "";
			width: 100%;
			height: rem(6);
			display: block;
			background: url('data:img/png;base64,') no-repeat;
			background-size: 100%;
			margin-top: rem(3);
		}
	}

	.listivo_icon_title {
		margin-bottom: rem(32);
	}
	
	.listivo_button_default {
		width: 100%;
		text-transform: uppercase;
	}
}

.listivo_block_dark {
	border-radius: rem(10);
	padding: rem(46);
	background-color: var(--color2);

	>.listivo_title {
		font-size: rem(20);
		font-weight: 700;
		font-family: $font-title;
		color: white;
		display: inline-block;
		margin-bottom: rem(20);

		@include tablet {
			font-size: rem(24);
		}

		&::after {
			content: "";
			width: 100%;
			height: rem(9);
			display: block;
			background: url('data:img/png;base64,') no-repeat;
			background-size: 100%;
			margin-top: rem(3);
		}
	}

	>.listivo_text {
		line-height: rem(28);
		color: white;
	}

	.listivo_field {
		margin-bottom: rem(14);

		&:first-child {
			margin-top: rem(36);
		}
	}

	.listivo_field_control {
		margin-top: rem(16);

		.listivo_button {
			width: 100%;
		}
	}
}

.listivo_list {

	li {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin-bottom: rem(20);

		&::before {
			content: "";
			width: rem(13);
			height: rem(13);
			background: url(../img/triangle.png) no-repeat;
			background-size: 100%;
			margin-right: rem(10);
		}

		&:last-child {
			margin-bottom: 0;
		}

		&:hover {

			&::before {
				background: url(../img/triangle3.png) no-repeat;
				background-size: 100%;
			}

			span {
				opacity: 1;
			}
		}

		a {
			color: var(--text-descr);
			font-weight: 500;
			flex: 1 0 0;
			display: flex;
			justify-content: space-between;
			span {
				// margin-left: auto;
				font-weight: 500;
				color: var(--text-descr);
				opacity: .3;
			}
		}

		
	}
}

.listivo_list.listivo_list_static {
	
	li {
		&:hover {

			&::before {
				background: url(../img/triangle.png) no-repeat;
				background-size: 100%;
			}
		}
	}
}

.listivo_aside_item {
	border-bottom: rem(1) solid #cee1e6;
	padding-bottom: rem(32);
	margin-bottom: rem(34);

	display: flex;
	flex-wrap: wrap;

	&:last-child {
		margin-bottom: 0;
		padding-bottom: 0;
		border-bottom: 0;
	}

	&:hover {
		.listivo_title {
			color: var(--red);
		}
	}

	&-image {
		flex: 0 0 rem(70);
		margin-right: rem(14);
		max-width: rem(70);
		height: rem(70);
		border-radius: 50%;
		overflow: hidden;
	}

	&-text {
		flex: 1 0 0;
		display: flex;
		flex-direction: column;

		.listivo_title {
			font-size: rem(18);
			line-height: rem(26);
			font-weight: 500;
			margin-bottom: rem(6);
		}

		span {

			font-size: rem(12);
		}
	}
}

.listivo_tags {
	display: flex;
	flex-wrap: wrap;

	a {
		display: flex;
		align-items: center;
		height: rem(44);
		padding: 0 rem(22);
		background-color: white;
		border-radius: rem(22);
		margin-bottom: rem(10);
		margin-right: rem(8);
		box-shadow: $item_shadow;

		font-size: rem(14);
		font-weight: 500;
		color: var(--text-descr);

		&:nth-child(2n) {
			margin-right: 0;
		}

		&:hover {
			color: white;
			background-color: var(--red);
			box-shadow: none;
		}
	}
}