.listivo_footer {
	position: relative;
	background-color: #061b23;

	&::before {
		content: "";
		position: absolute;
		left: 0;
		bottom: rem(105);
		width: 100%;
		height: rem(12);
		background: url(../img/footer_line.png) repeat-x;
		background-size: 100%;
	}

	&_top {
		padding-top: rem(86);
		padding-bottom: rem(96);

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		&-first {
			.listivo_footer_logo {
				margin-bottom: rem(34);
				display: block;
			}
		}

		&-control {

			.listivo_title {
				font-size: rem(24);
				font-weight: 700;
				font-family: 'Comfortaa';
				color: white;
				// margin-bottom: rem(32);
				margin: 0 0 rem(32) 0;
				display: block;
			}

			ul {

				li {
					margin-bottom: rem(26);

					&:last-child {
						margin-bottom: 0;
					}

					a {
						position: relative;
						color: #bababa;
						display: block;

						transition: all 300ms ease;

						&::before {
							content: "";
							position: absolute;
							top: rem(6);
							left: 0;
							

							width: rem(6);
							height: rem(6);
							display: block;
							border-radius: 50%;
							background-color: var(--red);
							opacity: 0;
							transition: all 300ms ease;
						}

						&:hover {
							padding-left: rem(16);
							color: white;

							&::before {
								opacity: 1;
							}
						}
					}
				}
			}
		}
	}

	&_bottom {
		text-align: center;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		padding-bottom: rem(50);

		> p, a {
			color: #bababa;
		}
		
		> a {
			margin-left: rem(12);
			text-decoration: underline;

			&:hover {
				text-decoration: none;
			}
		}
	}
}

.block_text-col {
	display: flex;
	flex-direction: column;
	

	span {
		&:first-child {
			font-weight: 500;
			color: white;
			line-height: rem(28);
		}

		&:last-child {
			color: #bbbbbb;
			line-height: rem(28);
			font-weight: 300;
		}
	}
}

.block_social_group {
	margin-top: rem(34);
	display: flex;
	flex-wrap: wrap;

	.social_item {
		margin-right: rem(5);
		width: rem(32);
		height: rem(32);
		border-radius: 50%;
		background-color: #0b4355;
		display: flex;
		align-items: center;
		justify-content: center;
		transition: all 300ms ease;

		i {
			color: white;
			font-size: rem(14);
		}

		&:hover {
			background-color: var(--red);
		}

		&:last-child {
			margin-right: 0;
		}
	}
}

.author_item {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: rem(24);

	&:last-of-type {
		margin-bottom: 0;
	}

	&-avatar {
		width: rem(50);
		height: rem(50);
		border-radius: 50%;
		overflow: hidden;
		margin-right: rem(18);
	}

	&-text {
		display: flex;
		flex-direction: column;

		> a {
			font-weight: 500;
			color: white;
			display: block;
			margin-bottom: rem(10);
		}

		p {
			color: #bababa;
			font-size: rem(14);
			margin-bottom: rem(14);
			display: block;

			> span {
				color: white;
			}

			> a {
				color: var(--red);
			}
		}
	}
}