.listivo_faq_page {
	margin-bottom: rem(120);

	.w-4\/12 {
		padding-left: rem(50);
	}
}

.listivo_accordion_item {
	width: 100%;
	background-color: white;
	box-shadow: 0 0 rem(26) rem(2.4) rgba(0, 0, 0, .08);
	border-radius: rem(10);
	margin-bottom: rem(30);
	padding: rem(26) rem(36);

	&:last-child {
		margin-bottom: 0;
	}

	&-button {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		padding: 0;
		width: 100%;
		justify-content: space-between;

		b {
			font-size: rem(18);
			font-weight: 700;
		}

		i {
			font-size: rem(18);
			color: var(--red);
		}
	}

	&-text {
		margin-top: rem(30);
		display: none;

		.listivo_text {
			line-height: rem(28);
			color: var(--text-descr);
		}
	}
}

.listivo_accordion_item.accordion_active {

	.listivo_accordion_item-button {

		b {
			color: var(--red);
		}

		i {
			font-family: 'Font Awesome 5 Pro';
			font-weight: 300;
			color: var(--text-color);

			&::before {
				content: '\f063';
			}
		}
	}

	.listivo_accordion_item-text {
		display: block;
	}
}