.listivo_title_block {
	margin: rem(56) 0;

	.listivo_title {
		font-size: rem(36);
		font-weight: 700;
		font-family: $font-title;
	}
}

.listivo_message_block {
	padding: rem(26) 0;
	border: rem(1) solid rgba(83,163,193,.2);
	border-radius: rem(10);
	background-color: #e6f6fc;
	text-align: center;

	p {
		a {
			color: var(--color2);
			text-decoration: underline;

			&:hover {
				text-decoration: none;
			}
		}
	}
}

.listivo_wrap_overlay {
	padding: rem(115) 0;
	background-color: #f2fafc;

	.listivo_message_block {
		margin-bottom: rem(55);
	}

	.listivo_white_block {
		margin-bottom: rem(58);

		&:last-of-type {
			margin-bottom: 0;
		}
	}
}

.listivo_white_block {
	background-color: white;
	border-radius: rem(10);
	padding: rem(58) rem(45);

	.listivo_title {
		font-size: rem(36);
		font-weight: 700;
		font-family: $font-title;
		margin-bottom: rem(46);
	}

	.listivo_checked {
		align-items: center;

		&-status {
			width: rem(30);
			height: rem(30);
		}
	}

	.listivo_button {
		text-transform: unset;
	}

	.listivo_field_one {
		margin-bottom: rem(30);

		&:last-child {
			margin-bottom: 0;
		}

		i,span {
			border-left: 0;
			font-weight: 300;
		}

		.listivo_checked {
			display: flex;
			flex-wrap: wrap;

			span {
				position: relative;
				right: auto;
				top: auto;
				padding-left: 0;
				color: var(--text-color);
			}
		}
	}

	.listivo_map {
		height: rem(400);
	}

	.listivo_col_3 {
		.listivo_field {
			padding-right: rem(68);
		}
	}

	.listivo_block_border {
		margin-bottom: rem(30);

		&:last-child {
			margin-bottom: 0;
		}
	}

	.w-custom {
		width: rem(510);
	}
}



.listivo_upload_wrap {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	align-items: center;
	padding: rem(30) 0;

	&-top {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin-bottom: rem(10);
		i {
			font-size: rem(30);
			color: var(--red);
			margin-right: rem(16);
		}

		span {
			font-size: rem(24);
			color: var(--text-descr);
		}
	}

	&-bottom {
		font-size: rem(18);
		color: rgba(68,68,68,.5);
	}

	&-count {
		position: absolute;
		right: 0;
		bottom: 0;

		span {
			font-size: rem(14);
			color: var(--text-descr);
		}
	}

	&-button {
		position: absolute;
		left: 0;
		bottom: 0;

		display: flex;
		align-items: center;

		i {
			font-size: rem(24);
			color: var(--text-descr);
			margin-right: rem(6);
		}

		span {
			font-size: rem(14);
			color: var(--text-descr);
		}
	}

	&-item {
		position: relative;
		border-radius: rem(7.5);
		width: rem(150);
		height: rem(150);
		overflow: hidden;

		margin-right: rem(10);
		margin-bottom: rem(20);

		@include tablet {
			width: rem(170);
			height: rem(170);
			margin-right: rem(20);
		}

		&:nth-child(6n) {
			margin-right: 0;
		}

		.listivo_upload_error {
			background-color: var(--red);
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;

			span {
				color: white;
				font-size: rem(14);
				line-height: rem(20);
				text-align: center;
				padding: 0 rem(22);
			}
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		.listivo_upload_delete {
			position: absolute;
			z-index: 1;
			top: rem(10);
			right: rem(10);
		}
	}

	.listivo_upload_delete {
		width: rem(32);
		height: rem(32);
		border-radius: 50%;
		background-color: white;
		display: flex;
		align-items: center;
		justify-content: center;
	}
}

.listivo_upload_list {
	flex-direction: row;
	padding-bottom: rem(60);

	.listivo_upload_wrap-count,
	.listivo_upload_wrap-button {
		bottom: rem(15);
	}
}