.listivo_auth_page {
	position: relative;
	width: 100%;
	background-color: #f2fafc;
	display: flex;
	justify-content: center;
	align-items: center;

	&::before {
		content: "";
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
		background: url(../img/auth_bg.png) no-repeat;
		background-size: cover;
		z-index: 0;
	}

	.listivo_auth_box {
		position: relative;
		z-index: 1;
		margin: rem(130) 0;
	}
}

.listivo_auth_box {
	width: rem(500);
	background-color: white;
	border-radius: rem(10);
	overflow: hidden;
	box-shadow: $item_shadow;

	&-nav {
		display: flex;
		flex-wrap: wrap;
		border-bottom: rem(1) solid #e4ebf0;
		margin-bottom: rem(45);

		a {
			position: relative;
			flex: 0 0 50%;
			height: rem(80);
			display: flex;
			align-items: center;
			justify-content: center;

			font-size: rem(20);
			font-weight: 700;
			font-family: $font-title;

			transition: all 300ms ease;

			&:hover {
				&::after {
					content: "";
					position: absolute;
					left: 0;
					bottom: 0;
					height: rem(2);
					width: 100%;
					background-color: var(--red);
				}
			}
		}

		.listivo_active {
			&::after {
				content: "";
				position: absolute;
				left: 0;
				bottom: 0;
				height: rem(2);
				width: 100%;
				background-color: var(--red);
			}
		}
	}

	&-social {
		padding: 0 rem(50);

		&_item {
			width: 100%;
			height: rem(60);
			border-radius: rem(10);
			border: rem(1) solid #e4ebf0;
			display: flex;
			align-items: center;
			justify-content: center;

			font-size: rem(18);
			font-weight: 500;

			margin-bottom: rem(12);

			&:last-child {
				margin-bottom: 0;
			}

			.listivo_auth-icon {
				margin-right: rem(12);
			}
		}
	}

	&-line {
		position: relative;
		width: 100%;
		height: rem(1);
		background-color: #e4ebf0;
		margin: rem(40) 0;
		text-align: center;

		span {
			position: absolute;
			z-index: 1;
			top: rem(-8);
			font-size: rem(14);
			font-weight: 500;
			text-transform: uppercase;

			&::before {
				content: "";
				position: absolute;
				z-index: -1;
				top: rem(7);
				left: rem(-40);
				width: rem(100);
				height: rem(2);
				background-color: white;
			}
		}
	}

	&-form {
		padding: 0 rem(50) rem(50) rem(50);
	}
}