.title_block {
	display: flex;
	flex-direction: column;
	align-items: center;

	&-top {
		margin-bottom: rem(16);
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		&::before {
			content: "";
			width: rem(25);
			height: rem(1);
			background: url(../img/title_line.png) no-repeat;
			background-size: 100%;
			margin-right: rem(14);
		}

		&::after {
			content: "";
			width: rem(25);
			height: rem(1);
			background: url(../img/title_line.png) no-repeat;
			background-size: 100%;
			margin-left: rem(14);
			transform: rotate(180deg);
		}

		.listivo_text {
			font-size: rem(18);
			font-weight: 300;
		}
	}
	&-bottom {
		position: relative;

		.listivo_text {
			font-size: rem(54);
			font-weight: 700;
			font-family: 'Comfortaa';
			text-align: center;
		}
	}

}


