.listivo_field_box {
	position: relative;
}

.listivo_field {
	width: 100%;
	height: rem(64);
	border-radius: rem(10);
	border: rem(1) solid #e4ebf0;
	background-color: white;
	padding: 0 rem(26);

	@include placeholder {
		color: var(--text-color);
		opacity: 1;
		transition: all 300ms ease;
	}

	&:focus {
		@include placeholder {
			opacity: 0;
		}
	} 
}

textarea.listivo_field {
	height: auto;
	padding: rem(20) rem(26);
	resize: vertical;

	min-height: rem(150);
	max-height: rem(250);
}

.listivo_field_group {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin-bottom: rem(26);

	&:last-child {
		margin-bottom: 0;
	}

	> div, > input {
		width: auto;
		flex-grow: 1;
		flex-shrink: 0;

		margin-right: rem(30);

		&:last-child {
			margin-right: 0;
		}
	}

	> .listivo_field {
		// width: 48%;
	}
}

.listivo_field_group.listivo_col_2 {
	>div,
	>input,
	>label {
		flex: 0 0 49%;
		max-width: 49%;

		margin-right: 0;
	}
}

.listivo_field_group.listivo_col_3 {

	>div,
	>input,
	>label {
		flex: 0 0 32%;
		max-width: 32%;

		margin-right: 0;
	}
}

.listivo_disabled {
	background-color: #f6f6f6;
	pointer-events: none;
}

.listivo_field_label {
	position: relative;

	label {
		position: absolute;
		top: rem(22);
		left: rem(26);
		color: var(--text-descr);
		opacity: 1;
		transition: all 300ms ease;

		span {
			color: var(--red);
		}
	}

	input {
		&:focus ~ label {
			
			opacity: 0;
		}
	}
}

.listivo_field_row {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(26);

	&:last-child {
