.listivo_checked {
	cursor: pointer;

	input {
		display: none;
	}

	display: flex;
	flex-wrap: wrap;

	input[type="checkbox"] ~ &-status {
		border-radius: rem(4);
	}

	input[type="radio"]~&-status {
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;

		&::before {
			content: "";
			position: absolute;
			width: rem(8);
			height: rem(8);
			border-radius: 50%;
		}
		
	}
	input[type="radio"]:checked ~ &-status {
		&::before {
			content: "";
			position: absolute;
			width: rem(8);
			height: rem(8);
			border-radius: 50%;
			background-color: var(--red);
		}
	}

	input[type="checkbox"]:checked~&-status {
		background-color: #f2fafc;
		display: flex;
		align-items: center;
		justify-content: center;

		&::before {
			content: "\f00c";
			position: absolute;
			font-family: 'Font Awesome 5 Pro';
			font-weight: 500;
			color: var(--color2);
			font-size: rem(12);
			
		}
	}

	&-status {
		position: relative;
		width: rem(20);
		height: rem(20);
		border: rem(1) solid #dddddd;
		margin-right: rem(10);
	}

	a {
		text-decoration: underline;

		&:hover {
			text-decoration: none;
		}
	}
}

