#listivo_our_team_header {
	margin-bottom: 0;
}

.lisitivo_items_team {
	margin-bottom: rem(70);
	.w-4\/12 {
		flex: 0 0 calc(33.333333% - 1.875rem) !important;
		max-width: calc(33.333333% - 1.875rem) !important;
	}
}

.listivo_our_team {
	background-color: #f2fafc;
	padding: rem(115) 0;
	margin-bottom: rem(115);

	

	.listivo_content {
		margin-top: 0;

		.listivo_title {
			position: relative;
			font-size: rem(36);
			line-height: rem(46);
			font-weight: 700;
			font-family: $font-title;

			&::after {
				content: "";
				position: absolute;
				top: rem(24);
				right: rem(50);
				width: rem(29);
				height: rem(29);
				background: url(../img/triangle2.png) no-repeat;
				background-size: 100%;

				@include tablet {
					right: rem(150);
				}
			}
		}
		.listivo_subtitle {
			position: relative;
			display: block;
			line-height: rem(32);
			color: var(--text-descr);
			margin-bottom: rem(30);

			&::after {
				content: "";
				width: rem(117);
				height: rem(6);
				display: block;
				background: url('data:img/png;base64,') no-repeat;
				background-size: 100%;
				margin-top: rem(3);
			}
		}

		.listivo_text {
			line-height: rem(28);
			margin-bottom: rem(42);
		}

		.listivo_quote {
			display: flex;
			flex-wrap: wrap;
			margin-bottom: rem(50);

			&-image {
				max-width: rem(58);
				flex: 0 0 rem(58);
			}

			&-text {
				padding-left: rem(24);
				flex: 1 0 0;
				border-left: rem(4) solid var(--color2);

				p {
					font-size: rem(18);
					line-height: rem(28);
					font-weight: 500;
					margin-bottom: rem(18);
				}
			}
		}

		.listivo_icon_text {
			margin-right: rem(16);

			&:last-child {
				margin-right: 0;
			}
		}

		.w-7\/12 {
			padding-left: rem(70);
			padding-right: rem(70);
		}
	}
}

.listivo_person {
	position: relative;
	// overflow: hidden;
	height: rem(440);
	border-radius: rem(10);

	display: flex;
	justify-content: center;

	@include tablet {
		height: rem(500);
	}

	&::after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 70%;
		z-index: 2;
		background-image: linear-gradient(0deg, rgb(0, 0, 0) 0%, rgba(0, 0, 0, 0) 100%);

		border-bottom-left-radius: rem(10);
		border-bottom-right-radius: rem(10);
	}

	&::before {
		content: "";
		position: absolute;
		top: rem(-56);
		left: rem(-62);
		width: rem(119);
		height: rem(129);
		z-index: 0;
		background: url(../img/item_vector.png) no-repeat;
		background-size: 100%;
	}

	&-image {
		position: relative;
		z-index: 1;
		width: 100%;
		height: 100%;
		border-radius: rem(10);
		overflow: hidden;
	}

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	&-control {
		position: absolute;
		z-index: 3;
		bottom: rem(50);

		

		.social_item {
			background-color: white !important;

			&:hover {
				background-color: var(--red) !important;

				i {
					color: white !important;
				}
			}

			i {
				color: var(--color2) !important;
			}
		}
	}

	.block_social_group {
		justify-content: center;
		margin-top: rem(22);
	}

	&-info {

		text-align: center;

		.listivo_title {
			font-size: rem(24);
			font-weight: 700;
			font-family: $font-title;
			color: white;
			// margin-bottom: rem(14);
		}

		span {
			line-height: rem(48);
			color: white;
		}
	}
}

.listivo_person-item {
	height: rem(360);
	overflow: hidden;
	margin-bottom: rem(50);

	@include tablet {
		height: rem(440);
	}

	&:hover {
		.listivo_person-control {
			transform: translateY(0);

			.listivo_phone {
				opacity: 1;
			}
		}
		
		
	}

	.listivo_person-control {
		transform: translateY(rem(110));
		transition: all 300ms ease;

		

		.listivo_phone {
			opacity: 0;
			transition: all 300ms ease;
		}
	}
}

.listivo_icon_text {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	&-left {
		width: rem(40);
		height: rem(40);
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #e6f4f8;

		i {
			color: var(--color2);
		}
	}

	&-right {
		margin-left: rem(12);

		span {
			font-weight: 600;
		}
	}
}

.listivo_phone {
	padding: rem(12) rem(18);
	background-color: rgba(255,255,255,.2);
	border-radius: rem(20);
	color: white;

	font-weight: 500;

	i {
		color: white;
		margin-right: rem(8);
	}
}