.listivo_send_message {
	padding: rem(120) 0;
	background-color: var(--color2_3);

	.title_block {
		position: relative;
		margin-bottom: rem(65);

		&::after {
			content: "";
			position: absolute;
			top: rem(20);
			right: rem(250);
			width: rem(83);
			height: rem(62);
			background: url(../img/arrow_red.png) no-repeat;
			background-size: 100%;

			transform: scaleY(-1);

			@include tablet {
				right: rem(320);
			}
		}
	}

}