#listing_archive_grid {

	.listovo_breadcrumb_wrap {
		background-color: white;
	}

	.listivo_listing_filter-top, .listivo_listing_filter-bottom {
		background-color: #f2fafc;
	}

	.listivo_listing_filter-top {
		position: relative;

		&::before {
			content: "";
			position: absolute;
			bottom: rem(-12);
			left: 0;
			width: 100%;
			height: rem(12);
			background: url(../img/header_line_blue.png) no-repeat;
			background-size: 100%;
		}
	}
}

.listivo_items-listview {
	.card_item {
		width: 100%;
		flex-direction: row;
		flex-wrap: wrap;

		&-top {
			width: rem(300);
			height: rem(295);
			border-top-right-radius: 0;
			border-bottom-right-radius: 0;
		}

		&-bottom {
			display: flex;
			flex-wrap: wrap;
			flex-direction: column;
			flex: 1 0 0;

			&::after {
				display: none;
			}

			&_head {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;
			}

			.listivo_text {
				line-height: rem(28);
				color: var(--text-descr);
				margin-bottom: rem(36);
				padding: 0 rem(32);
			}

			> .flex {
				padding: 0 rem(32);
			}
		}
	}
}

.listivo_listing_filter {
	position: relative;

	&-top, &-bottom {
		height: rem(158);
	}

	&-top {
		background-color: #e8f6fa;
		display: flex;
		align-items: center;

		.container-min {
			position: relative;
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			
			&::before {
				content: "";
				position: absolute;
				top: 0;
				left: rem(-258);
				width: rem(127);
				height: rem(30);
				background: url(../img/cloud.png) no-repeat;
				background-size: 100%;

				@include tablet {
					display: none;
				}
			}

			&::after {
				content: "";
				position: absolute;
				top: rem(20);
				left: rem(-112);
				width: rem(83);
				height: rem(62);
				background: url(../img/arrow_red.png) no-repeat;
				background-size: 100%;

				transform: rotate(-75deg) scaleY(-1);
			}
		}

		.listivo_field_box {
			flex: 0 0 49%;
			height: rem(68);
			border-radius: rem(10);
			border: rem(1) solid #4b889c;
			background-color: white;
			box-shadow: 0 0 rem(27) rem(2.4) rgba(5, 35, 44, .13);

			display: flex;
			flex-wrap: wrap;
			align-items: center;

			i {
				position: absolute;
				top: rem(26);
				left: rem(28);
			}

			.listivo_field {
				border-color: transparent !important;
				padding-left: rem(52);
				font-size: rem(16);

				height: rem(28);
			}

			&:last-child {
				.listivo_field {
					width: 80%;
					border-radius: 0;
					border-right: rem(1) solid #e4ebf0 !important;
				}

				.custom-select {
					width: 20%;
					height: rem(28);

					.custom-select__option--value {
						border-color: transparent;
						border-radius: 0;
						height: rem(30);

						&:hover {
							background-color: transparent;
						}
					}
				}
			}
		}
	}

	&-bottom {
		background-color: #f2fafc;
		display: flex;
		align-items: center;

		.container-min {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
		}

		.listivo_field,
		.custom-select {
			flex: 0 0 24%;
			max-width: 24%;
		}
	}
}