.listivo_header {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	margin-top: rem(47);
	margin-bottom: rem(42);

	&_logo {
		max-width: rem(124);
		margin-right: rem(90);
	}

	&_nav {
		margin-right: auto;

		> ul {
			display: flex;
			flex-wrap: wrap;

			> li {
				margin-right: rem(36);

				&:last-child {
					margin-right: 0;
				}

				a {
					
					font-weight: 500;
					i {
						font-size: rem(14);
						margin-left: rem(12);
					}

					&:hover {
						color: var(--red);
					}
				}
			}
			
		}
	}

	&_right {

		&-control {
			display: flex;
			flex-wrap: wrap;
			

			> ul {
				display: flex;
				flex-wrap: wrap;
				// align-items: center;
				margin-right: rem(22);

				> li {
					position: relative;
					display: flex;
					align-items: center;
					margin-right: rem(30);

					&::after {
						content: "";
						position: absolute;
						top: 50%;
						margin-top: rem(-9);
						right: rem(-15);
						height: rem(18);
						width: rem(1);
						background-color: #cccccc;
					}

					&:last-child {
						margin-right: 0;

						&::after {
							display: none;
						}
					}

					> a {
						display: flex;
						align-items: center;

						span {
							margin-left: rem(12);
						}

						&:hover {
							color: var(--red);
						}
					}
				}
			}

		}
	}
}

.header_bg {
	background-color: var(--color2);
	height: rem(105);
	margin-top: 0;
	margin-bottom: 0;

	ul li a {
		color: white;
	}

	.container-max {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
	}
}