.listivo_header_revision {
	width: 100%;
	position: relative;
	height: rem(240);
	margin-bottom: rem(110);

	.revision_image {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: -1;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(4,24,30,.75);
		}
	}

	.container-min {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		height: 100%;
		align-items: center;
	}

	.listivo_title {
		font-size: rem(54);
		font-weight: 700;
		font-family: $font-title;
		color: white;
	}

	.listovo_breadcrumb {
		span {
			color: #cccccc;
		}
		a {
			color: white;
			&::before {
				color: white;
			}
		}
	}
}