.listivo_author_right {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin-bottom: rem(30);
	padding-bottom: rem(20);
	border-bottom: rem(1) solid #cee1e6;

	&-text {
		.listivo_title {
			font-size: rem(24);
			font-weight: 500;
			margin-bottom: rem(8);
		}

		span {
			font-size: rem(14);
			color: var(--text-descr);
		}
	}

	&-image {
		width: rem(60);
		height: rem(60);
		border-radius: 50%;
		overflow: hidden;
	}
}

.listivo_phone_hidden {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	.listivo_image {
		width: rem(22);
		margin-right: rem(12);
	}

	.listivo_text {
		flex: 1 0 0;
		b {
			font-size: rem(21);
			font-weight: 800;
			color: var(--red);

			@include tablet {
				font-size: rem(24);
			}
		}

		span {
			margin-left: rem(12);
			font-size: rem(18);
		}
	}
}

.listivo_icon_title {
	display: flex;
	align-items: center;

	i {
		font-size: rem(18);
		color: var(--red);
		margin-right: rem(8);
	}

	a {
		margin-left: rem(12);

		&:hover {
			text-decoration: underline;
		}
	}
}

.listivo_article_main {
	border: 0;
	margin-bottom: rem(100);

	.listivo_article_page-header {
		margin-bottom: rem(30);

		h1 {
			font-size: rem(30);
			margin-bottom: 0;
		}

		> span {
			color: #878787;
			font-size: rem(14);
			margin-bottom: rem(12);
			display: block;
		}

		> .flex {

			&:first-of-type {
				margin-bottom: rem(16);
			}

			&:last-of-type {
				a {
					color: #437cc3;

					&:hover {
						text-decoration: underline;
					}
				}
			}

			b {
				font-weight: 400;
				font-family: $font-title;
				font-size: rem(36);
			}
		}

	}

	.swiper-container {
		.swiper-slide {
			height: rem(460);
			border-radius: rem(10);
			overflow: hidden;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.swiper-pagination-custom {
			position: absolute;
			z-index: 1;
			width: auto;
			bottom: rem(24);
			left: rem(24);

			background-color: black;
			border-radius: rem(5);
			color: white;
			font-size: rem(12);
			font-weight: 500;
			padding: rem(7) rem(12);
		}

		.slider_arrows {
			position: absolute;
			top: 50%;
			margin-top: rem(-16);
			width: 100%;
			z-index: 1;
			

			&-left, &-right {
				position: absolute;
				background-color: white;
				border: rem(1) solid #e4ebf0;

				i {
					color: var(--text-descr);
				}
			}

			&-left {
				left: rem(24);
			}

			&-right {
				right: rem(24);
			}
		}
	}

	.listivo_block_blue {
		margin: rem(54) 0;

		.listivo_title {
			&::after {
				display: none;
			}
		}
	}

	.listivo_field_group {

		>div,
		>input {
			&:nth-child(2n) {
				margin-right: 0;
			}
		}

		.listivo_field_one {
			margin-bottom: rem(16);
		}
	}
}

.listivo_wrap_border {
	border: rem(2) solid #d4e8ed;
	padding: rem(38);
	border-radius: rem(8);

	display: flex;
	flex-wrap: wrap;

	.listivo_total {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

		flex: 0 0 33%;
		max-width: 33%;
		height: rem(90);

		border-right: rem(2) solid #d4e8ed;

		&:last-child {
			border-right: 0;
		}

		span {
			margin-bottom: rem(16);
		}

		b {
			font-size: rem(24);
			font-weight: 700;
			font-family: $font-title;
			color: var(--red);
		}
	}
}

.listivo_tags {
	display: flex;
	flex-wrap: wrap;
	margin: rem(30) 0;

	.listivo_tag_item {
		border: rem(1) solid #cccccc;
		border-radius: rem(5);
		font-weight: 500;
		padding: rem(20) rem(22);
		margin-right: rem(14);

		&:last-child {
			margin-right: 0;
		}
	}
}

.listivo_field_one {
	position: relative;

	label {
		margin-bottom: rem(16);
		display: block;
		 
		span {
			color: var(--red);
		}
	}

	i, span {
		position: absolute;
		top: rem(18);
		right: rem(20);

		height: rem(28);
		display: flex;
		align-items: center;
		padding-left: rem(20);
		border-left: rem(1) solid #e4ebf0;

		color: var(--text-descr);
		font-size: rem(18);
	}
}

.p-46 {
	padding: rem(46);
}

.listivo_article_location {
	margin-bottom: rem(45);

	.listivo_title {
		margin-bottom: rem(34);
		font-size: rem(36);
		font-weight: 700;
		font-family: $font-title;
	}
}

.listivo_article_footer {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;

	b {
		font-weight: 500;
	}
}

.listivo_featured_article,
.listivo_related_article {

	background-color: white;
	margin-bottom: 0;

	&::before {
		display: none;
	}

	.swiper-container,
	.slider_arrows {
		display: flex;
	}

	.title_block {
		.listivo_text {
			font-size: rem(30);
			font-weight: 700;
			font-family: $font-title;

			&::after {
				content: "";
				width: 100%;
				height: rem(6);
				display: block;
				background: url(../img/line3.png) no-repeat;
				background-size: 100%;
				margin-top: rem(3);
			}
		}
	}

	.slider_arrows {
		margin-top: 0;
	}
}

.listivo_featured_article {
	border-top: rem(1) solid #e4ebf0;
}

.listivo_related_article {
	background-color: #f2fafc;
	
}