input[type="checkbox"] ~ .listivo_checked-status {
  border-radius: 0.25rem;
}

input[type="radio"] ~ .listivo_checked-status {
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

input[type="radio"] ~ .listivo_checked-status::before {
  content: "";
  position: absolute;
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
}

input[type="radio"]:checked ~ .listivo_checked-status::before {
  content: "";
  position: absolute;
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
  background-color: var(--red);
}

input[type="checkbox"]:checked ~ .listivo_checked-status {
  background-color: #f2fafc;
  display: flex;
  align-items: center;
  justify-content: center;
}

input[type="checkbox"]:checked ~ .listivo_checked-status::before {
  content: "\f00c";
  position: absolute;
  font-family: 'Font Awesome 5 Pro';
  font-weight: 500;
  color: var(--color2);
  font-size: 0.75rem;
}

